<template lang="">
  <div class="_full_router modifiyPassBody">
    <custHeader :title="title"></custHeader>
    <mt-field label="*原密码" placeholder="请输入原密码" type="password" v-model="oldPass" v-validtype="rules.oldPass" :state="rules.oldPass.state"></mt-field>
    <mt-field label="*新密码" placeholder="请输入新密码" type="password" v-model="newPass"v-validtype="rules.newPass" :state="rules.newPass.state" ></mt-field>
    <mt-field label="*确认密码" placeholder="请再输入一遍新密码" type="password" v-model="comfirmPass" v-validtype="rules.comfirmPass" :state="rules.comfirmPass.state"></mt-field>
    <mt-button  type="danger" size="large" class="modifiy" @click="modifiy">修改</mt-button>
  </div>
</template>

<script>
import custHeader from  'components/custHeader'
import {MessageBox} from 'mint-ui'
export default {
  data(){
    return {
      title:'修改登录密码',
      newPass:'',
      oldPass:'',
      comfirmPass:'',
      rules:{
        newPass:{str:"required",state:""},
        oldPass:{str:"required",state:""},
        comfirmPass:{str:"required",state:""},
      }
    }
  },
  methods:{
    modifiy:function(){
      var self = this;
      if(this.newPass == this.comfirmPass){
        if(this.rules.newPass.state == "success" &&this.rules.oldPass.state == "success"  &&this.rules.comfirmPass.state == "success" ){
          if(global.isOpenMd5){
            self.oldPass=global.md5(self.oldPass);
            self.newPass=global.md5(self.newPass);
          }
          axiosUtil.upCltPasswordUpd({useScope:'0',authType:'1',oldPassword:self.oldPass,newPassword:self.newPass}).then(data => {
            var data = data.data
            if(data.errorcode == 0){
              MessageBox.alert("修改成功").then(action => {
                router.push("/personal/userInfo")
              })
            }else{
              MessageBox("提示",data.errormsg)
            }
          })
        }else{
          MessageBox("请将信息填写完整")
        }
      }else{
        this.rules.comfirmPass.state = 'error'
      }


    }
  },
  components:{
     custHeader
   },
}
</script>

<style lang="css">
.modifiyPassBody{
  background-color: #eee;
}
.modifiy{
  position: fixed;
  bottom: 0;
  width: 100%;
}
</style>



